/* eslint-disable no-shadow */
/* eslint-disable @typescript-eslint/explicit-module-boundary-types */
import React, { useState } from "react";
import Paper from "@material-ui/core/Paper";
import {
  EditingState,
  SortingState,
  IntegratedSorting,
  SearchState,
  IntegratedFiltering,
} from "@devexpress/dx-react-grid";
import {
  Grid,
  TableHeaderRow,
  TableEditColumn,
  VirtualTable,
  Toolbar,
  SearchPanel,
} from "@devexpress/dx-react-grid-material-ui";
import { Command, PopupEditing } from "../../Shared";
import { useCustomers } from "../../hooks";
import { getRowId } from "../../common";
import { PopupCustomer } from "../../pubups";
import { AlertLocal, SearchTable } from "../../components";
import { errorAlert, errorDeleteAlert } from "../../Shared/helpers";
import PageLayout from "../main/PageLayout";

export default function Customers(props: any) {
  const { isRTL, words, isEditor, theme, menuitem } = props;
  const [alrt, setAlrt] = useState({ show: false, msg: "", type: undefined });

  const [columns] = useState([
    { name: isRTL ? "nameAr" : "name", title: words.name },
    { name: "phone", title: words.phoneNumber },
    { name: "mobile", title: words.mobile },
    { name: "email", title: words.email },
    { name: "address", title: words.theaddress },
  ]);

  const {
    customers,
    addCustomer,
    editCustomer,
    removeCustomer,
    refreshcustomer,
  } = useCustomers();

  const commitChanges = async ({ deleted }) => {
    if (deleted) {
      const _id = deleted[0];
      const res = await removeCustomer({ variables: { _id } });
      if (res?.data?.deleteCustomer?.ok === false) {
        if (res?.data?.deleteCustomer?.error.includes("related")) {
          await errorDeleteAlert(setAlrt, isRTL);
        } else {
          await errorAlert(setAlrt, isRTL);
        }
      }
    }
  };

  return (
    <PageLayout
      menuitem={menuitem}
      isRTL={isRTL}
      words={words}
      isEditor={isEditor}
      theme={theme}
      refresh={refreshcustomer}
    >
      <Paper>
        <Grid rows={customers} columns={columns} getRowId={getRowId}>
          <SortingState />
          <SearchState />
          <EditingState onCommitChanges={commitChanges} />

          <IntegratedSorting />
          <IntegratedFiltering />

          <VirtualTable
            height={window.innerHeight - 133}
            messages={{
              noData: isRTL ? "  " : "no data",
            }}
            estimatedRowHeight={30}
          />
          <TableHeaderRow showSortingControls />
          {isEditor && (
            <TableEditColumn
              showEditCommand
              showDeleteCommand
              showAddCommand
              commandComponent={Command}
            ></TableEditColumn>
          )}
          <Toolbar />
          <SearchPanel
            inputComponent={(props: any) => {
              return <SearchTable isRTL={isRTL} {...props}></SearchTable>;
            }}
          />
          <PopupEditing
            theme={theme}
            addAction={addCustomer}
            editAction={editCustomer}
          >
            <PopupCustomer></PopupCustomer>
          </PopupEditing>
        </Grid>
        {alrt.show && (
          <AlertLocal
            isRTL={isRTL}
            type={alrt?.type}
            msg={alrt?.msg}
            top
          ></AlertLocal>
        )}
      </Paper>
    </PageLayout>
  );
}
