import { gql } from "@apollo/client";

export default gql`
  mutation createDepartment(
    $branch: String
    $name: String
    $nameAr: String
    $desc: String
    $color: String
  ) {
    createDepartment(
      branch: $branch
      name: $name
      nameAr: $nameAr
      desc: $desc
      color: $color
    ) {
      ok
      message
      data
      error
    }
  }
`;
