import { gql } from "@apollo/client";

export default gql`
  mutation updateDepartment(
    $_id: String
    $branch: String
    $name: String
    $nameAr: String
    $desc: String
    $color: String
  ) {
    updateDepartment(
      _id: $_id
      branch: $branch
      name: $name
      nameAr: $nameAr
      desc: $desc
      color: $color
    ) {
      ok
      message
      data
      error
    }
  }
`;
