/* eslint-disable @typescript-eslint/explicit-module-boundary-types */
import React, { useState } from "react";
import Paper from "@material-ui/core/Paper";
import {
  EditingState,
  SortingState,
  IntegratedSorting,
  DataTypeProvider,
  SearchState,
  IntegratedFiltering,
} from "@devexpress/dx-react-grid";
import {
  Grid,
  TableHeaderRow,
  TableEditColumn,
  VirtualTable,
  Toolbar,
  SearchPanel,
} from "@devexpress/dx-react-grid-material-ui";
import { Command, errorAlert, PopupEditing } from "../../Shared";
import { useDepartments } from "../../hooks";
import { getRowId } from "../../common";
import { PopupDeprtment } from "../../pubups";
import { avatarPatternFormatter } from "../../Shared/colorFormat";
import { AlertLocal, SearchTable } from "../../components";
import { errorDeleteAlert } from "../../Shared/helpers";

export default function Departments({ isRTL, words, isEditor, theme }: any) {
  const [alrt, setAlrt] = useState({ show: false, msg: "", type: undefined });

  const [columns] = useState([
    { name: isRTL ? "nameAr" : "name", title: words.name },
    { name: isRTL ? "name" : "nameAr", title: words.name },
    { name: "desc", title: words.description },
    { name: "avatar", title: words.color },
  ]);

  const { departments, addDepartment, editDepartment, removeDepartment } =
    useDepartments();

  const commitChanges = async ({ deleted }) => {
    if (deleted) {
      const _id = deleted[0];
      const res = await removeDepartment({ variables: { _id } });
      if (res?.data?.deleteDepartment?.ok === false) {
        if (res?.data?.deleteDepartment?.error.includes("related")) {
          await errorDeleteAlert(setAlrt, isRTL);
        } else {
          await errorAlert(setAlrt, isRTL);
        }
      }
    }
  };

  return (
    <Paper>
      <Grid rows={departments} columns={columns} getRowId={getRowId}>
        <SortingState />
        <SearchState />
        <EditingState onCommitChanges={commitChanges} />

        <IntegratedSorting />
        <IntegratedFiltering />

        <VirtualTable
          height={window.innerHeight - 133}
          messages={{
            noData: isRTL ? "  " : "no data",
          }}
          estimatedRowHeight={30}
        />

        <TableHeaderRow showSortingControls />
        <DataTypeProvider
          for={["avatar"]}
          formatterComponent={avatarPatternFormatter}
        ></DataTypeProvider>

        {isEditor && (
          <TableEditColumn
            showEditCommand
            showDeleteCommand
            showAddCommand
            commandComponent={Command}
          ></TableEditColumn>
        )}

        <Toolbar />
        <SearchPanel
          inputComponent={(props: any) => {
            return <SearchTable isRTL={isRTL} {...props}></SearchTable>;
          }}
        />

        <PopupEditing
          theme={theme}
          addAction={addDepartment}
          editAction={editDepartment}
        >
          <PopupDeprtment></PopupDeprtment>
        </PopupEditing>
      </Grid>
      {alrt.show && (
        <AlertLocal
          isRTL={isRTL}
          type={alrt?.type}
          msg={alrt?.msg}
          top
        ></AlertLocal>
      )}
    </Paper>
  );
}
