/* eslint-disable @typescript-eslint/explicit-module-boundary-types */
import React, { useState } from "react";
import Paper from "@material-ui/core/Paper";
import {
  EditingState,
  SortingState,
  IntegratedSorting,
  DataTypeProvider,
  SearchState,
  IntegratedFiltering,
} from "@devexpress/dx-react-grid";
import {
  Grid,
  TableHeaderRow,
  TableEditColumn,
  VirtualTable,
  Toolbar,
  SearchPanel,
} from "@devexpress/dx-react-grid-material-ui";
import { Command, PopupEditing } from "../../Shared";
import { getRowId } from "../../common";
import { PopupEmployee } from "../../pubups";
import {
  avatarPatternFormatter,
  colorFormatter,
  daysoffFormatter,
} from "../../Shared/colorFormat";
import { AlertLocal, SearchTable } from "../../components";
import { errorAlert, errorDeleteAlert } from "../../Shared/helpers";
import useHResourses from "../../hooks/useHResourses";

export default function Employees({ isRTL, words, isEditor, theme }: any) {
  const [alrt, setAlrt] = useState({ show: false, msg: "", type: undefined });

  const [columns] = useState([
    { name: isRTL ? "nameAr" : "name", title: words.name },
    { name: "avatar", title: words.color },
    { name: "phone", title: words.phoneNumber },
    { name: "email", title: words.email },
    {
      name: isRTL ? "departmentNameAr" : "departmentName",
      title: words.department,
    },
    { name: "info", title: words.info },
    { name: "daysoff", title: isRTL ? " " : "Day Off" },
  ]);

  const { employees, addEmployee, editEmployee, removeEmployee } =
    useHResourses();

  const commitChanges = async ({ deleted }) => {
    if (deleted) {
      const _id = deleted[0];
      const res = await removeEmployee({ variables: { _id } });
      if (res?.data?.deleteEmployee?.ok === false) {
        if (res?.data?.deleteEmployee?.error.includes("related")) {
          await errorDeleteAlert(setAlrt, isRTL);
        } else {
          await errorAlert(setAlrt, isRTL);
        }
      }
    }
  };

  return (
    <Paper>
      <Grid rows={employees} columns={columns} getRowId={getRowId}>
        <SortingState />
        <SearchState />

        <EditingState onCommitChanges={commitChanges} />

        <IntegratedSorting />
        <IntegratedFiltering />

        <VirtualTable
          height={window.innerHeight - 133}
          messages={{
            noData: isRTL ? "  " : "no data",
          }}
          estimatedRowHeight={30}
        />
        <TableHeaderRow showSortingControls />
        <DataTypeProvider
          for={["avatar"]}
          formatterComponent={avatarPatternFormatter}
        ></DataTypeProvider>
        <DataTypeProvider
          for={["color"]}
          formatterComponent={colorFormatter}
        ></DataTypeProvider>
        <DataTypeProvider
          for={["daysoff"]}
          formatterComponent={(props: any) =>
            daysoffFormatter({ ...props, isRTL })
          }
        ></DataTypeProvider>
        {isEditor && (
          <TableEditColumn
            showEditCommand
            showDeleteCommand
            showAddCommand
            commandComponent={Command}
          ></TableEditColumn>
        )}
        <Toolbar />
        <SearchPanel
          inputComponent={(props: any) => {
            return <SearchTable isRTL={isRTL} {...props}></SearchTable>;
          }}
        />

        <PopupEditing
          theme={theme}
          addAction={addEmployee}
          editAction={editEmployee}
        >
          <PopupEmployee></PopupEmployee>
        </PopupEditing>
      </Grid>
      {alrt.show && (
        <AlertLocal
          isRTL={isRTL}
          type={alrt?.type}
          msg={alrt?.msg}
          top
        ></AlertLocal>
      )}
    </Paper>
  );
}
