/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable @typescript-eslint/explicit-module-boundary-types */
import { useEffect, useState } from "react";
import {
  Grid,
  Table,
  TableHeaderRow,
  TableEditColumn,
} from "@devexpress/dx-react-grid-material-ui";
import { Box, fade, Paper, Typography, withStyles } from "@material-ui/core";
import {
  DataTypeProvider,
  EditingState,
  IntegratedSorting,
  SortingState,
} from "@devexpress/dx-react-grid";
import { Getter } from "@devexpress/dx-react-core";
import { getColumns } from "../common/columns";
import {
  createdAtFormatter,
  currencyFormatter,
  eventStatusFormatter,
  fromToFormatter,
} from "./colorFormat";
import { useMutation } from "@apollo/client";
import { createEvent, deleteEventById, updateEvent } from "../graphql";
import { Command } from "./Command";
import PopupEditing from "./PopupEditing";
import PopupAppointmentTask from "../pubups/PopupAppointmentTask";
import getTaskEvents from "../graphql/query/getTaskEvents";
import getTasks from "../graphql/query/getTasks";
export const getRowId = (row: any) => row._id;

const NumberTypeProvider = (props) => (
  <DataTypeProvider
    formatterComponent={({ value }) => (
      <Typography variant="subtitle2">{Number(value) + 1}</Typography>
    )}
    {...props}
  />
);

const styles = (theme) => ({
  tableStriped: {
    "& tbody tr:nth-of-type(odd)": {
      backgroundColor: fade(theme.palette.primary.main, 0.1),
    },
  },
});

const TableComponentBase = ({ classes, ...restProps }) => (
  <Table.Table {...restProps} className={classes.tableStriped} />
);

export const TableComponent = withStyles(styles, { name: "TableComponent" })(
  TableComponentBase
);

export default function EventsTable({
  open,
  isTaskNew,
  rows,
  isRTL,
  words,
  isEditor,
  employees,
  departments,
  customers,
  servicesproducts,
  theme,
  taskId,
  isNew,
  employee,
  department,
  customer,
  company,
}: any) {
  const col = getColumns({ isRTL, words });

  const [columns, setColumns] = useState([
    col.startDate,
    col.fromto,
    col.docNo,
    col.department,
    col.employee,
    col.status,
    col.amount,
  ]);

  const refresQuery = {
    refetchQueries: [
      {
        query: getTaskEvents,
        variables: { taskId },
      },
      {
        query: getTasks,
      },
    ],
  };

  useEffect(() => {
    if (isTaskNew) {
      setColumns([col.startDate, col.fromto, col.status, col.amount]);
    }
  }, [open]);

  const [addEvent] = useMutation(createEvent, refresQuery);
  const [editEvent] = useMutation(updateEvent, refresQuery);
  const [removeEventById] = useMutation(deleteEventById, refresQuery);

  const commitChanges = async ({ deleted }) => {
    if (deleted) {
      const _id = deleted[0];
      removeEventById({ variables: { _id } });
    }
  };

  const renderEmpty = () => {
    return <Box display="flex"></Box>;
  };

  return (
    <Paper
      style={{
        maxHeight: 265,
        overflow: "auto",
        margin: 10,
        minHeight: 265,
      }}
    >
      {rows && (
        <Grid rows={rows} columns={columns} getRowId={getRowId}>
          <SortingState />
          <EditingState onCommitChanges={commitChanges} />
          <IntegratedSorting />
          <Table
            noDataCellComponent={renderEmpty}
            tableComponent={TableComponent}
          />
          <DataTypeProvider
            for={["startDate"]}
            formatterComponent={createdAtFormatter}
          ></DataTypeProvider>
          <DataTypeProvider
            for={["fromto"]}
            formatterComponent={fromToFormatter}
          ></DataTypeProvider>
          <DataTypeProvider
            for={["status"]}
            formatterComponent={eventStatusFormatter}
          ></DataTypeProvider>
          <DataTypeProvider
            for={["amount"]}
            formatterComponent={currencyFormatter}
          ></DataTypeProvider>
          <NumberTypeProvider for={["index"]} />
          <TableHeaderRow showSortingControls />

          {isEditor && !isNew && (
            <TableEditColumn
              showEditCommand
              showDeleteCommand={rows && rows.length > 1}
              showAddCommand
              commandComponent={Command}
            ></TableEditColumn>
          )}

          <PopupEditing addAction={addEvent} editAction={editEvent}>
            <PopupAppointmentTask
              employees={employees}
              departments={departments}
              customers={customers}
              servicesproducts={servicesproducts}
              theme={theme}
              taskId={taskId}
              employee={employee}
              department={department}
              customer={customer}
              company={company}
              isEditor={isEditor}
            ></PopupAppointmentTask>
          </PopupEditing>

          <Getter
            name="tableColumns"
            computed={({ tableColumns }) => {
              const result = [
                {
                  key: "editCommand",
                  type: TableEditColumn.COLUMN_TYPE,
                  width: isNew ? 20 : 100,
                },
                ...tableColumns.filter(
                  (c: any) => c.type !== TableEditColumn.COLUMN_TYPE
                ),
              ];
              return result;
            }}
          />
        </Grid>
      )}
    </Paper>
  );
}
