/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable @typescript-eslint/explicit-module-boundary-types */
import React, { useContext, useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import {
  successAlert,
  dublicateAlert,
  errorAlert,
  yup,
  messageAlert,
} from "../Shared";
import { GContextTypes } from "../types";
import { GlobalContext } from "../contexts";
import { FormControlLabel, Radio, RadioGroup } from "@material-ui/core";
import useAccounts from "../hooks/useAccounts";
import { parents } from "../constants/kaid";
import PopupLayout from "../pages/main/PopupLayout";
import { Grid } from "@material-ui/core";
import { CalenderLocal, TextFieldLocal } from "../components";
import { getAppStartEndPeriod } from "../common/time";
import AutoFieldLocal from "../components/fields/AutoFieldLocal";
import { useDepartments, useEmployees } from "../hooks";

const PopupExpenses = ({
  open,
  onClose,
  row,
  isNew,
  addAction,
  editAction,
  theme,
  tasks,
}: any) => {
  const [alrt, setAlrt] = useState({ show: false, msg: "", type: undefined });
  const [selectedDate, setSelectedDate] = React.useState(new Date());
  const [debaccounts, setDebaccounts] = React.useState([]);
  const [cridaccounts, setCridaccounts] = React.useState([]);

  const [debitAcc, setDebitAcc]: any = React.useState(null);
  const [creditAcc, setCreditAcc]: any = React.useState(null);

  const [emplvalue, setEmplvalue] = useState<any>(null);
  const [emplError, setEmplError] = useState<any>(false);

  const [resType, setResType] = useState<any>(null);
  const [emplslist, setEmplslist] = useState<any>([]);

  const [departvalue, setDepartvalue] = useState<any>(null);
  const [departError, setDepartError] = useState<any>(false);

  const [taskvalue, setTaskvalue] = useState<any>(null);

  const { employees } = useEmployees();
  const { departments } = useDepartments();

  const { register, handleSubmit, errors, reset } = useForm(
    yup.depositResolver
  );

  const {
    translate: { words, isRTL },
    store: { user },
  }: GContextTypes = useContext(GlobalContext);

  const { accounts } = useAccounts();

  useEffect(() => {
    if (employees && employees.length > 0) {
      const filtered = employees.filter((emp: any) => emp.resType === resType);
      setEmplslist(filtered);
    }
  }, [resType, employees]);

  useEffect(() => {
    if (row && row._id) {
      const ca = row.creditAcc;
      const da = row.debitAcc;
      const depId = row.departmentId;
      const empId = row.employeeId;
      const taskId = row.taskId;

      if (depId) {
        const depart = departments.filter((dep: any) => dep._id === depId)[0];
        setDepartvalue(depart);
      }
      if (empId) {
        const empl = employees.filter((emp: any) => emp._id === empId)[0];
        setEmplvalue(empl);
      }
      if (taskId) {
        const tsk = tasks.filter((ts: any) => ts.id === taskId)[0];
        setTaskvalue(tsk);
      }
      const filtereddebits = accounts.filter(
        (acc: any) =>
          acc.parentcode === parents.EXPENCES && acc.branch === "branch1"
      );
      setDebaccounts(filtereddebits);
      const filteredcredit = accounts.filter(
        (acc: any) =>
          acc.parentcode === parents.CASH && acc.branch === "branch1"
      );
      setCridaccounts(filteredcredit);

      if (ca) {
        const credit = accounts.filter(
          (acc: any) => acc.code === ca && acc.branch === "branch1"
        )[0];
        setCreditAcc(credit);
      }
      if (da) {
        const debit = accounts.filter(
          (acc: any) => acc.code === da && acc.branch === "branch1"
        )[0];
        setDebitAcc(debit);
      }
    } else {
      const filtereddebits = accounts.filter(
        (acc: any) =>
          acc.parentcode === parents.EXPENCES && acc.branch === "branch1"
      );
      setDebaccounts(filtereddebits);
      const filteredcredit = accounts.filter(
        (acc: any) =>
          acc.parentcode === parents.CASH && acc.branch === "branch1"
      );
      setCridaccounts(filteredcredit);
      setCreditAcc(filteredcredit?.[0]);
      setDebitAcc(filtereddebits?.[3]);
    }
  }, [row, open]);

  const onSubmit = async (data: any) => {
    const { startPeriod, endPeriod } = getAppStartEndPeriod();
    if (selectedDate < startPeriod || selectedDate > endPeriod) {
      await messageAlert(
        setAlrt,
        isRTL ? "  " : "Date should be change"
      );
      return;
    }
    const { amount, desc } = data;
    if (!debitAcc || !creditAcc) {
      await messageAlert(
        setAlrt,
        isRTL ? "   " : "You have to select both accounts"
      );
      return;
    }
    if (debitAcc === creditAcc) {
      await messageAlert(
        setAlrt,
        isRTL ? "    " : "The accounts must be deferent"
      );
      return;
    }
    const department = departvalue
      ? {
          departmentId: departvalue._id,
          departmentName: departvalue.name,
          departmentNameAr: departvalue.nameAr,
          departmentColor: departvalue.color,
        }
      : {
          departmentId: undefined,
          departmentName: undefined,
          departmentNameAr: undefined,
          departmentColor: undefined,
        };
    const employee = emplvalue
      ? {
          employeeId: emplvalue._id,
          employeeName: emplvalue.name,
          employeeNameAr: emplvalue.nameAr,
          employeeColor: emplvalue.color,
        }
      : {
          employeeId: undefined,
          employeeName: undefined,
          employeeNameAr: undefined,
          employeeColor: undefined,
        };

    const variables: any = {
      _id: row && row._id ? row._id : undefined, // is it new or edit
      time: selectedDate,
      debitAcc: debitAcc.code,
      creditAcc: creditAcc.code,
      amount,
      taskId: taskvalue ? taskvalue.id : undefined,
      department,
      employee,
      desc,
      branch: user.branch,
      userId: user._id,
    };

    const mutate = isNew ? addAction : editAction;
    apply(mutate, variables);
  };

  const apply = async (mutate: any, variables: any) => {
    try {
      await mutate({ variables });
      await successAlert(setAlrt, isRTL);
      closeModal();
    } catch (error) {
      onError(error);
    }
  };

  const onError = async (error: any) => {
    if (error.message.includes("duplicate")) {
      await dublicateAlert(setAlrt, isRTL);
    } else {
      await errorAlert(setAlrt, isRTL);
      console.log(error);
    }
  };

  const resetAll = () => {
    reset();
    setCreditAcc(null);
    setDebitAcc(null);
    setDebaccounts([]);
    setCridaccounts([]);
    setEmplvalue(null);
    setEmplError(false);
    setDepartvalue(null);
    setDepartError(false);
    setResType(null);
    setEmplslist([]);
    setTaskvalue(null);
  };
  const closeModal = () => {
    resetAll();
    onClose();
  };

  const handleDateChange = (date: any) => {
    setSelectedDate(date);
  };

  const onHandleSubmit = () => {
    handleSubmit(onSubmit)();
  };

  return (
    <PopupLayout
      isRTL={isRTL}
      open={open}
      onClose={closeModal}
      title={words.expenses}
      onSubmit={onHandleSubmit}
      theme={theme}
      alrt={alrt}
      mt={10}
    >
      <Grid container spacing={2}>
        <Grid item xs={1}></Grid>
        <Grid item xs={9}>
          <Grid container spacing={2}>
            <Grid item xs={12}>
              <CalenderLocal
                isRTL={isRTL}
                label={words.time}
                value={selectedDate}
                onChange={handleDateChange}
              ></CalenderLocal>
            </Grid>
            <Grid item xs={6}>
              <AutoFieldLocal
                name="debitAcc"
                title={isRTL ? " " : "Expenses Acc"}
                words={words}
                options={debaccounts}
                value={debitAcc}
                setSelectValue={setDebitAcc}
                register={register}
                isRTL={isRTL}
                fullwidtth
              ></AutoFieldLocal>
            </Grid>
            <Grid item xs={6}>
              <AutoFieldLocal
                name="creditAcc"
                title={isRTL ? " " : "Payment Acc"}
                words={words}
                options={cridaccounts}
                value={creditAcc}
                setSelectValue={setCreditAcc}
                register={register}
                isRTL={isRTL}
                fullwidtth
              ></AutoFieldLocal>
            </Grid>

            <Grid item xs={12}>
              <TextFieldLocal
                required
                name="amount"
                label={words.amount}
                register={register}
                errors={errors}
                row={row}
                type="number"
                width={203}
              />
              <AutoFieldLocal
                name="task"
                title={words.task}
                words={words}
                options={tasks}
                value={taskvalue}
                setSelectValue={setTaskvalue}
                register={register}
                isRTL={isRTL}
                fullWidth
              ></AutoFieldLocal>
              <AutoFieldLocal
                name="department"
                title={words.department}
                words={words}
                options={departments}
                value={departvalue}
                setSelectValue={setDepartvalue}
                setSelectError={setDepartError}
                selectError={departError}
                register={register}
                isRTL={isRTL}
                mb={20}
              ></AutoFieldLocal>
              <RadioGroup
                aria-label="Views"
                name="views"
                row
                value={resType}
                onChange={(e: any) => {
                  setResType(Number(e.target.value));
                  setEmplvalue(null);
                }}
              >
                <FormControlLabel
                  value={1}
                  control={<Radio color="primary" />}
                  label={isRTL ? "" : "Employees"}
                />

                <FormControlLabel
                  value={2}
                  control={<Radio color="primary" />}
                  label={isRTL ? "" : "Resourses"}
                />
              </RadioGroup>
              <AutoFieldLocal
                name="employee"
                title={resType === 1 ? words.employee : words.resourses}
                disabled={!resType}
                words={words}
                options={emplslist}
                value={emplvalue}
                setSelectValue={setEmplvalue}
                setSelectError={setEmplError}
                selectError={emplError}
                register={register}
                isRTL={isRTL}
                mb={20}
              ></AutoFieldLocal>

              <TextFieldLocal
                name="desc"
                multiline
                rows={4}
                label={words.description}
                register={register}
                errors={errors}
                row={row}
                fullWidth
              />
            </Grid>
          </Grid>
        </Grid>
        <Grid item xs={2}></Grid>
      </Grid>
    </PopupLayout>
  );
};

export default PopupExpenses;
