import { gql } from "@apollo/client";

export default gql`
  query getCustomers($isRTL: Boolean) {
    getCustomers(isRTL: $isRTL) {
      ok
      error
      data {
        _id
        branch
        autoNo
        docNo
        name
        nameAr
        phone
        mobile
        address
        email

        employeeId
        employeeName
        employeeNameAr
        employeeColor
        employeePhone

        userId

        createdAt
        updatedAt
      }
    }
  }
`;
