/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable @typescript-eslint/explicit-module-boundary-types */
import React, { useContext, useEffect, useState } from "react";
import Paper from "@material-ui/core/Paper";
import {
  EditingState,
  SortingState,
  IntegratedSorting,
  DataTypeProvider,
  SearchState,
  IntegratedFiltering,
} from "@devexpress/dx-react-grid";
import {
  Grid,
  TableHeaderRow,
  TableEditColumn,
  VirtualTable,
  Toolbar,
  SearchPanel,
} from "@devexpress/dx-react-grid-material-ui";
import { Command, Loading, PopupEditing } from "../../Shared";
import { getLandingChartData } from "../../graphql";
import { useLazyQuery, useMutation } from "@apollo/client";
import {
  createdAtFormatter,
  currencyFormatter,
  eventStatusFormatter,
  progressFormatter,
  taskNameFormatter,
} from "../../Shared/colorFormat";
import PageLayout from "../main/PageLayout";
import { SearchTable } from "../../components";
import { getColumns } from "../../common/columns";
import { Box, Button, useMediaQuery } from "@material-ui/core";
import TasksContext from "../../contexts/tasks";
import getTasks from "../../graphql/query/getTasks";
import PopupTask from "../../pubups/PopupTask";
import createTask from "../../graphql/mutation/createTask";
import updateTask from "../../graphql/mutation/updateTask";
import deleteTaskById from "../../graphql/mutation/deleteTaskById";
import { useCustomers } from "../../hooks";
import PopupGantt from "../../pubups/PopupGantt";

export const getRowId = (row: { _id: any }) => row._id;

export default function Tasks({
  isRTL,
  words,
  menuitem,
  isEditor,
  theme,
  employees,
  departments,
  company,
  servicesproducts,
}) {
  const col = getColumns({ isRTL, words });
  const isMobile = useMediaQuery("(max-width:600px)");

  const [columns] = useState([
    col.title,
    col.docNo,
    col.start,
    col.end,
    col.customer,
    col.department,
    col.employee,
    col.progress,
    col.evQty,
    col.amount,
  ]);

  const [rows, setRows] = useState([]);
  const [loading, setLoading] = useState(false);
  const [openGantt, setOpenGantt] = useState(false);

  const [periodvalue, setPeriodvalue] = useState<any>(null);

  const {
    state: { sort },
    dispatch,
  } = useContext(TasksContext);

  const [loadTasks, eventsData]: any = useLazyQuery(getTasks, {
    fetchPolicy: "cache-and-network",
  });

  const refresQuery = {
    refetchQueries: [
      {
        query: getTasks,
      },
      {
        query: getLandingChartData,
      },
    ],
  };

  useEffect(() => {
    loadTasks({});
  }, []);

  const [addTask] = useMutation(createTask, refresQuery);
  const [editTask] = useMutation(updateTask, refresQuery);
  const [removeTaskById] = useMutation(deleteTaskById, refresQuery);
  const { customers, addCustomer, editCustomer } = useCustomers();

  const commitChanges = async ({ deleted }) => {
    if (deleted) {
      const _id = deleted[0];
      removeTaskById({ variables: { _id } });
    }
  };

  useEffect(() => {
    if (eventsData?.loading) {
      setLoading(true);
    }
    if (eventsData?.data?.getTasks?.data) {
      const { data } = eventsData.data.getTasks;
      setRows(data);
      setLoading(false);
    }
  }, [eventsData]);

  const refresh = () => {
    eventsData?.refetch();
  };

  const setSortDispatch = (value: any) => {
    dispatch({ type: "setSort", payload: value });
  };

  return (
    <PageLayout
      menuitem={menuitem}
      isRTL={isRTL}
      words={words}
      isEditor={isEditor}
      theme={theme}
      refresh={refresh}
      periodvalue={periodvalue}
      setPeriodvalue={setPeriodvalue}
    >
      <>
        <Box
          style={{
            position: "absolute",
            marginTop: 10,
            marginRight: 10,
            zIndex: 100,
          }}
        >
          <Button
            variant="contained"
            color="primary"
            onClick={() => setOpenGantt(true)}
          >
            {isRTL ? " " : "Task View"}
          </Button>
        </Box>
        <Paper>
          <Grid rows={rows} columns={columns} getRowId={getRowId}>
            <SortingState
              defaultSorting={sort}
              onSortingChange={(srt: any) => setSortDispatch(srt)}
            />
            <EditingState onCommitChanges={commitChanges} />
            {!isMobile && <SearchState />}

            <IntegratedSorting />
            {!isMobile && <IntegratedFiltering />}

            <VirtualTable
              height={window.innerHeight - 133}
              messages={{
                noData: isRTL ? "  " : "no data",
              }}
              estimatedRowHeight={30}
            />
            <TableHeaderRow showSortingControls />

            <DataTypeProvider
              for={["start", "end"]}
              formatterComponent={createdAtFormatter}
            ></DataTypeProvider>
            <DataTypeProvider
              for={["status"]}
              formatterComponent={eventStatusFormatter}
            ></DataTypeProvider>
            <DataTypeProvider
              for={["amount"]}
              formatterComponent={currencyFormatter}
            ></DataTypeProvider>
            <DataTypeProvider
              for={["tasktype"]}
              formatterComponent={taskNameFormatter}
            ></DataTypeProvider>
            <DataTypeProvider
              for={["progress"]}
              formatterComponent={progressFormatter}
            ></DataTypeProvider>

            {isEditor && (
              <TableEditColumn
                showEditCommand
                showDeleteCommand
                showAddCommand
                commandComponent={Command}
              ></TableEditColumn>
            )}

            {!isMobile && <Toolbar />}
            {!isMobile && (
              <SearchPanel
                inputComponent={(props: any) => {
                  return <SearchTable isRTL={isRTL} {...props}></SearchTable>;
                }}
              />
            )}
            <PopupEditing addAction={addTask} editAction={editTask}>
              <PopupTask
                employees={employees}
                departments={departments}
                customers={customers}
                addCustomer={addCustomer}
                editCustomer={editCustomer}
                company={company}
                servicesproducts={servicesproducts}
                theme={theme}
                isEditor={isEditor}
              ></PopupTask>
            </PopupEditing>
          </Grid>
          {loading && <Loading isRTL={isRTL} />}
          <PopupGantt
            open={openGantt}
            onClose={() => setOpenGantt(false)}
            tasks={rows}
            theme={theme}
            isRTL={isRTL}
          ></PopupGantt>
        </Paper>
      </>
    </PageLayout>
  );
}
