/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable @typescript-eslint/explicit-module-boundary-types */
import React, { useContext, useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import {
  successAlert,
  dublicateAlert,
  errorAlert,
  yup,
  messageAlert,
} from "../Shared";
import { GContextTypes } from "../types";
import { GlobalContext } from "../contexts";
import useAccounts from "../hooks/useAccounts";
import { operationTypes } from "../constants";
import { parents } from "../constants/kaid";
import PopupLayout from "../pages/main/PopupLayout";
import { Grid } from "@material-ui/core";
import { CalenderLocal, TextFieldLocal } from "../components";
import { getAppStartEndPeriod } from "../common/time";
import AutoFieldLocal from "../components/fields/AutoFieldLocal";
import { useCustomers } from "../hooks";

const PopupReceipt = ({
  open,
  onClose,
  row,
  isNew,
  addAction,
  editAction,
  theme,
  tasks,
}: any) => {
  const [alrt, setAlrt] = useState({ show: false, msg: "", type: undefined });
  const [selectedDate, setSelectedDate] = React.useState(new Date());
  const [ptype, setPtype] = React.useState("deposit");
  const [debaccounts, setDebaccounts] = React.useState([]);

  const [debitAcc, setDebitAcc]: any = React.useState(null);
  const [creditAcc, setCreditAcc]: any = React.useState(null);

  const [custvalue, setCustvalue] = useState<any>(null);
  const [custError, setCustError] = useState<any>(false);

  const [taskvalue, setTaskvalue] = useState<any>(null);

  const { register, handleSubmit, errors, reset } = useForm(
    yup.depositResolver
  );

  const {
    translate: { words, isRTL },
    store: { user },
  }: GContextTypes = useContext(GlobalContext);

  const { accounts } = useAccounts();
  const { customers } = useCustomers();

  useEffect(() => {
    if (row && row._id) {
      const ca = row.creditAcc;
      const da = row.debitAcc;
      const taskId = row.taskId;

      if (taskId) {
        const tsk = tasks.filter((ts: any) => ts.id === taskId)[0];
        setTaskvalue(tsk);
      }

      const filteredcredit = accounts.filter(
        (acc: any) =>
          acc.parentcode === parents.CASH && acc.branch === "branch1"
      );
      setDebaccounts(filteredcredit);

      if (ca) {
        const credit = accounts.filter(
          (acc: any) => acc.code === ca && acc.branch === "branch1"
        )[0];
        setCreditAcc(credit);
      }
      if (da) {
        const debit = accounts.filter(
          (acc: any) => acc.code === da && acc.branch === "branch1"
        )[0];
        setDebitAcc(debit);
      }
      const customerId = row?.customerId;
      if (customerId) {
        const cust = customers.filter((it: any) => it._id === customerId)[0];
        setCustvalue(cust);
      }
    } else {
      const filtereddebits = accounts.filter(
        (acc: any) =>
          acc.parentcode === parents.ACCOUNTS_RECEIVABLE &&
          acc.branch === "branch1"
      );
      const filteredcredit = accounts.filter(
        (acc: any) =>
          acc.parentcode === parents.CASH && acc.branch === "branch1"
      );
      setDebaccounts(filteredcredit);
      setDebitAcc(filteredcredit?.[0]);
      setCreditAcc(filtereddebits?.[0]);
    }
  }, [row, ptype, open]);

  const onSubmit = async (data: any) => {
    const { startPeriod, endPeriod } = getAppStartEndPeriod();
    if (selectedDate < startPeriod || selectedDate > endPeriod) {
      await messageAlert(
        setAlrt,
        isRTL ? "  " : "Date should be change"
      );
      return;
    }
    const { amount, desc } = data;
    if (!debitAcc || !creditAcc || !custvalue) {
      await messageAlert(
        setAlrt,
        isRTL ? "   " : "You have to select both accounts"
      );
      return;
    }

    const customer = custvalue
      ? {
          customerId: custvalue._id,
          customerName: custvalue.name,
          customerNameAr: custvalue.nameAr,
          customerPhone: custvalue.color,
        }
      : {
          customerId: undefined,
          customerName: undefined,
          customerNameAr: undefined,
          customerPhone: undefined,
        };

    const variables: any = {
      _id: row && row._id ? row._id : undefined, // is it new or edit
      opType: operationTypes.customerReceipt,
      time: selectedDate,
      debitAcc: debitAcc.code,
      creditAcc: creditAcc.code,
      taskId: taskvalue ? taskvalue.id : undefined,
      customer,
      amount,
      desc,
      branch: user.branch,
      userId: user._id,
    };
    const mutate = isNew ? addAction : editAction;
    apply(mutate, variables);
  };

  const apply = async (mutate: any, variables: any) => {
    try {
      await mutate({ variables });
      await successAlert(setAlrt, isRTL);
      closeModal();
    } catch (error) {
      onError(error);
    }
  };

  const onError = async (error: any) => {
    if (error.message.includes("duplicate")) {
      await dublicateAlert(setAlrt, isRTL);
    } else {
      await errorAlert(setAlrt, isRTL);
      resetAll();
      console.log(error);
    }
  };

  const resetAll = () => {
    reset();
    setPtype("deposit");
    setCreditAcc(null);
    setDebitAcc(null);
    setDebaccounts([]);
    setCustvalue(null);
    setTaskvalue(null);
    setCustError(false);
  };
  const closeModal = () => {
    resetAll();
    onClose();
  };

  const handleDateChange = (date: any) => {
    setSelectedDate(date);
  };

  const onHandleSubmit = () => {
    handleSubmit(onSubmit)();
  };

  return (
    <PopupLayout
      isRTL={isRTL}
      open={open}
      onClose={closeModal}
      title={words.receipts}
      onSubmit={onHandleSubmit}
      theme={theme}
      alrt={alrt}
      mt={10}
    >
      <Grid container spacing={2}>
        <Grid item xs={1}></Grid>
        <Grid item xs={10}>
          <Grid container spacing={2}>
            <Grid item xs={12}>
              <CalenderLocal
                isRTL={isRTL}
                label={words.time}
                value={selectedDate}
                onChange={handleDateChange}
              ></CalenderLocal>
            </Grid>
            <Grid item xs={6}>
              <AutoFieldLocal
                name="customer"
                title={words.customer}
                words={words}
                options={customers}
                value={custvalue}
                setSelectValue={setCustvalue}
                setSelectError={setCustError}
                selectError={custError}
                isRTL={isRTL}
                fullwidth
              ></AutoFieldLocal>
            </Grid>
            <Grid item xs={6}>
              <AutoFieldLocal
                name="debitAcc"
                title={isRTL ? " " : "Receipt Acc"}
                words={words}
                options={debaccounts}
                value={debitAcc}
                setSelectValue={setDebitAcc}
                register={register}
                isRTL={isRTL}
                fullwidth
              ></AutoFieldLocal>
            </Grid>
            <Grid item xs={12}>
              <TextFieldLocal
                required
                name="amount"
                label={words.amount}
                register={register}
                errors={errors}
                row={row}
                type="number"
                fullWidth
              />
              <AutoFieldLocal
                name="task"
                title={words.task}
                words={words}
                options={tasks}
                value={taskvalue}
                setSelectValue={setTaskvalue}
                register={register}
                isRTL={isRTL}
                fullWidth
              ></AutoFieldLocal>
              <TextFieldLocal
                name="desc"
                multiline
                rows={4}
                label={words.description}
                register={register}
                errors={errors}
                row={row}
                fullWidth
              />
            </Grid>
          </Grid>
        </Grid>
        <Grid item xs={1}></Grid>
      </Grid>
    </PopupLayout>
  );
};

export default PopupReceipt;
