import { gql } from "@apollo/client";

export default gql`
  mutation createEmployee(
    $branch: String
    $name: String
    $nameAr: String
    $color: String
    $resType: Int
    $department: DepartmentInput
    $comPercent: Float
    $phone: String
    $email: String
    $daysoff: String
    $info: String
  ) {
    createEmployee(
      branch: $branch
      name: $name
      nameAr: $nameAr
      color: $color
      resType: $resType
      department: $department
      comPercent: $comPercent
      phone: $phone
      email: $email
      daysoff: $daysoff
      info: $info
    ) {
      ok
      message
      data
      error
    }
  }
`;
