/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable @typescript-eslint/explicit-module-boundary-types */
import React, { useContext, useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import { dublicateAlert, errorAlert, messageAlert } from "../Shared";
import { GContextTypes } from "../types";
import { GlobalContext } from "../contexts";
import { Box, Button, Typography } from "@material-ui/core";
import PopupLayout from "../pages/main/PopupLayout";
import { Grid } from "@material-ui/core";
import AutoFieldLocal from "../components/fields/AutoFieldLocal";
import { CalenderLocal, TextFieldLocal } from "../components";
import { eventStatus, weekdaysNNo } from "../constants/datatypes";
import { getAppStartEndPeriod } from "../common/time";
import LoadingInline from "../Shared/LoadingInline";
import { useLazyQuery } from "@apollo/client";
import { moneyFormat } from "../Shared/colorFormat";
import PopupTaskAppointment from "./PopupTaskAppointment";
import EventsTable from "../Shared/EventsTable";
import _ from "lodash";
import getTaskEvents from "../graphql/query/getTaskEvents";
import PopupTaskInvoice from "./PopupTaskInvoice";
import getTaskItems from "../graphql/query/getTaskItems";

export const indexTheList = (list: any) => {
  return list.map((item: any, index: any) => {
    return {
      ...item,
      index,
    };
  });
};

const PopupTask = ({
  open,
  onClose,
  row,
  isNew,
  addAction,
  editAction,
  employees,
  departments,
  customers,
  servicesproducts,
  theme,
  isEditor,
  company,
}: any) => {
  const [loading, setLoading] = useState(false);
  const [saving, setSaving] = useState(false);

  const [tasktitle, setTasktitle]: any = useState(null);
  const [start, setStart]: any = useState(null);
  const [end, setEnd]: any = useState(null);

  const [departvalue, setDepartvalue] = useState<any>(null);
  const [departError, setDepartError] = useState<any>(false);
  const departRef: any = React.useRef();

  const [emplvalue, setEmplvalue] = useState<any>(null);
  const [emplError, setEmplError] = useState<any>(false);
  const emplRef: any = React.useRef();

  const [custvalue, setCustvalue] = useState<any>(null);
  const [custError, setCustError] = useState<any>(false);
  const custRef: any = React.useRef();

  const [status, setStatus]: any = useState(null);

  const [openEvent, setOpenEvent] = useState<any>(false);
  const [evList, setEvList] = useState<any>([]);
  const [total, setTotal] = useState<any>(null);

  const [openInvoice, setOpenInvoice] = useState(false);
  const [itemsList, setItemsList] = useState<any>([]);

  const [alrt, setAlrt] = useState({ show: false, msg: "", type: undefined });

  const { register, handleSubmit, reset } = useForm({});
  const {
    translate: { words, isRTL },
  }: GContextTypes = useContext(GlobalContext);

  const [getEvents, eventsData]: any = useLazyQuery(getTaskEvents, {
    fetchPolicy: "cache-and-network",
  });
  const [getItems, itemsData]: any = useLazyQuery(getTaskItems, {
    fetchPolicy: "cache-and-network",
  });

  useEffect(() => {
    const events = eventsData?.data?.["getTaskEvents"]?.data || [];
    const items = itemsData?.data?.["getTaskItems"]?.data || [];
    if (items && items.length > 0) {
      const ids = items.map((it: any) => it.itemId);
      const servlist = servicesproducts.filter((ser: any) =>
        ids.includes(ser._id)
      );

      const itemsWqtyprice = items.map((item: any, index: any) => {
        const {
          categoryId,
          categoryName,
          categoryNameAr,
          departmentId,
          departmentName,
          departmentNameAr,
          departmentColor,
          employeeId,
          employeeName,
          employeeNameAr,
          employeeColor,
        } = item;
        const serv = servlist.filter((se: any) => se._id === item.itemId)[0];
        return {
          ...serv,
          categoryId,
          categoryName,
          categoryNameAr,
          departmentId,
          departmentName,
          departmentNameAr,
          departmentColor,
          employeeId,
          employeeName,
          employeeNameAr,
          employeeColor,
          index,
          itemprice: item.itemPrice,
          itemqty: item.qty,
          itemtotal: item.total,
          itemtotalcost: item.qty * serv.cost,
        };
      });
      const finalItems = _(itemsWqtyprice)
        .groupBy("_id")
        .map((array, key) => ({
          name: array[0].name,
          nameAr: array[0].nameAr,
          categoryId: array[0].categoryId,
          categoryName: array[0].categoryName,
          categoryNameAr: array[0].categoryNameAr,
          departmentId: array[0].departmentId,
          departmentName: array[0].departmentName,
          departmentNameAr: array[0].departmentNameAr,
          departmentColor: array[0].departmentColor,
          employeeId: array[0].employeeId,
          employeeName: array[0].employeeName,
          employeeNameAr: array[0].employeeNameAr,
          employeeColor: array[0].employeeColor,
          autoNo: array[0].autoNo,
          docNo: array[0].docNo,
          cost: array[0].cost,
          itemType: array[0].itemType,
          index: array[0].index,
          itemprice: array[0].itemprice,
          itemqty: _.sumBy(array, "itemqty"),
          itemtotal: _.sumBy(array, "itemtotal"),
        }))
        .orderBy("index")
        .value();
      setItemsList(finalItems);
    }
    if (events && events.length > 0) {
      setEvList(indexTheList(events));
      setLoading(false);
    }
  }, [eventsData, itemsData]);

  useEffect(() => {
    if (isNew) {
      const start = new Date();
      const end = new Date();
      start.setMinutes(0);
      end.setDate(end.getDate() + 7);
      end.setMinutes(0);
      setStart(start);
      setEnd(end);
      setStatus(eventStatus.filter((es: any) => es.id === 2)?.[0]);
    }
  }, [open]);

  useEffect(() => {
    getOverallTotal();
  }, [evList]);

  useEffect(() => {
    if (row && row._id) {
      setLoading(true);
      const variables = { taskId: row.id };
      getEvents({ variables });
      getItems({ variables });
      const depId = row.departmentId;
      const empId = row.employeeId;
      const custId = row.customerId;
      const statNo = row.status;

      setStart(row?.start);
      setEnd(row?.end);
      setTasktitle(row?.title);
      if (depId) {
        const depart = departments.filter((dep: any) => dep._id === depId)[0];
        setDepartvalue(depart);
      }
      if (empId) {
        const empl = employees.filter((emp: any) => emp._id === empId)[0];
        setEmplvalue(empl);
      }
      if (custId) {
        const cust = customers.filter((cu: any) => cu._id === custId)[0];
        setCustvalue(cust);
      }
      if (statNo) {
        const stat = eventStatus.filter((es: any) => es.id === statNo)[0];
        setStatus(stat);
      }
    }
  }, [row]);

  const getOverallTotal = () => {
    const evssum = _.sumBy(evList, "amount");
    setTotal(evssum);
  };

  const resetAllForms = () => {
    setStart(null);
    setEnd(null);
    setCustvalue(null);
    setDepartvalue(null);
    setEmplvalue(null);
    setStatus(null);
    setTasktitle(null);
    setEvList([]);
    setLoading(false);
  };

  const onSubmit = async () => {
    const { startPeriod, endPeriod } = getAppStartEndPeriod();
    if (
      start < startPeriod ||
      start > endPeriod ||
      end < startPeriod ||
      end > endPeriod
    ) {
      await messageAlert(
        setAlrt,
        isRTL ? "  " : "Date should be change"
      );
      return;
    }
    if (start > end) {
      await messageAlert(
        setAlrt,
        isRTL ? "  " : "Date should be change"
      );
      return;
    }

    if (!tasktitle) {
      await messageAlert(
        setAlrt,
        isRTL ? "   " : "Please add Task title"
      );
      return;
    }

    if (!custvalue) {
      await messageAlert(
        setAlrt,
        isRTL ? "   " : "Please add Customer"
      );
      return;
    }
    if (!evList || evList.length === 0) {
      await messageAlert(
        setAlrt,
        isRTL ? "  " : "Please add Appointment"
      );
      return;
    }
    setSaving(true);
    const events =
      evList && evList.length > 0 ? JSON.stringify(evList) : undefined;
    const variables: any = {
      id: row && row.id ? row.id : undefined, // is it new or edit
      title: tasktitle ? tasktitle : custvalue?.name,
      start,
      end,
      amount: total,
      status: status ? status.id : 2,
      tasktype: 2, // 1: single event, 2: multi events, 3: no events - only items and time
      events,
      evQty: evList.length,
      evDone: 0,
      customer: custvalue
        ? {
            customerId: custvalue._id,
            customerName: custvalue.name,
            customerNameAr: custvalue.nameAr,
            customerPhone: custvalue.phone,
          }
        : {
            customerId: undefined,
            customerName: undefined,
            customerNameAr: undefined,
            customerPhone: undefined,
          },

      department: departvalue
        ? {
            departmentId: departvalue._id,
            departmentName: departvalue.name,
            departmentNameAr: departvalue.nameAr,
            departmentColor: departvalue.color,
          }
        : {
            departmentId: undefined,
            departmentName: undefined,
            departmentNameAr: undefined,
            departmentColor: undefined,
          },
      employee: emplvalue
        ? {
            employeeId: emplvalue._id,
            employeeName: emplvalue.name,
            employeeNameAr: emplvalue.nameAr,
            employeeColor: emplvalue.color,
            employeePhone: emplvalue.phone,
          }
        : {
            employeeId: undefined,
            employeeName: undefined,
            employeeNameAr: undefined,
            employeeColor: undefined,
            employeePhone: undefined,
          },
    };
    const mutate = isNew ? addAction : editAction;

    apply(mutate, variables);
  };

  const apply = async (mutate: any, variables: any) => {
    try {
      await mutate({ variables });
      setSaving(false);
      onCloseForm();
    } catch (error) {
      onError(error);
      console.log(error);
    }
  };

  const onError = async (error: any) => {
    if (error.message.includes("duplicate")) {
      await dublicateAlert(setAlrt, isRTL);
    } else {
      await errorAlert(setAlrt, isRTL);
      reset();
      console.log(error);
    }
  };

  const onCloseForm = () => {
    resetAllForms();
    onClose();
  };

  const onHandleSubmit = () => {
    handleSubmit(onSubmit)();
  };

  const date = row?.start ? new Date(row?.start) : new Date();
  const day = weekdaysNNo?.[date.getDay()];

  const title = isRTL
    ? isNew
      ? " "
      : " "
    : isNew
    ? "New Task"
    : "Edit Task";

  return (
    <PopupLayout
      isRTL={isRTL}
      open={open}
      onClose={onCloseForm}
      title={title}
      onSubmit={onHandleSubmit}
      theme={theme}
      alrt={alrt}
      mt={10}
      maxWidth="lg"
      preventclose
      saving={saving}
    >
      <>
        <Box display="flex">
          <Typography style={{ fontWeight: "bold" }} variant="body2">
            {row?.docNo}
          </Typography>
        </Box>

        <Grid container spacing={2}>
          <Grid item xs={12} md={6}>
            <CalenderLocal
              isRTL={isRTL}
              label={words.start}
              value={start}
              onChange={(d: any) => setStart(d)}
              format="dd/MM/yyyy - hh:mm"
              time
            ></CalenderLocal>
          </Grid>
          <Grid item xs={12} md={6}>
            <CalenderLocal
              isRTL={isRTL}
              label={words.end}
              value={end}
              onChange={(d: any) => setEnd(d)}
              format="dd/MM/yyyy - hh:mm"
              time
            ></CalenderLocal>
          </Grid>
          <Grid item xs={12} md={12}>
            <Grid container spacing={2}>
              <Grid item xs={6}>
                <TextFieldLocal
                  required
                  name="tasktitle"
                  label={words.title}
                  value={tasktitle}
                  onChange={(e: any) => setTasktitle(e.target.value)}
                  row={row}
                  fullWidth
                  mb={0}
                />
              </Grid>
              <Grid item xs={6}>
                <AutoFieldLocal
                  name="customer"
                  title={words.customer}
                  words={words}
                  options={customers}
                  value={custvalue}
                  setSelectValue={setCustvalue}
                  setSelectError={setCustError}
                  selectError={custError}
                  refernce={custRef}
                  register={register}
                  isRTL={isRTL}
                  fullWidth
                  showphone
                ></AutoFieldLocal>
              </Grid>
            </Grid>

            <Grid container spacing={2}>
              <Grid item xs={6}>
                <AutoFieldLocal
                  name="department"
                  title={words.department}
                  words={words}
                  options={departments}
                  value={departvalue}
                  setSelectValue={setDepartvalue}
                  setSelectError={setDepartError}
                  selectError={departError}
                  refernce={departRef}
                  register={register}
                  noPlus
                  isRTL={isRTL}
                  fullWidth
                ></AutoFieldLocal>
              </Grid>
              <Grid item xs={6}>
                <AutoFieldLocal
                  name="employee"
                  title={words.employee}
                  words={words}
                  options={employees}
                  value={emplvalue}
                  setSelectValue={setEmplvalue}
                  setSelectError={setEmplError}
                  selectError={emplError}
                  refernce={emplRef}
                  register={register}
                  noPlus
                  isRTL={isRTL}
                  fullWidth
                  day={day}
                ></AutoFieldLocal>
              </Grid>
            </Grid>

            <Box
              style={{
                backgroundColor: "#f4f4f4",
                padding: 10,
                marginTop: 15,
                marginBottom: 15,
                borderRadius: 10,
              }}
            >
              <Box
                display="flex"
                style={{
                  alignItems: "center",
                  justifyContent: "flex-start",
                  marginInlineStart: 10,
                }}
              >
                {isNew && (
                  <Button
                    color="primary"
                    onClick={() => setOpenEvent(true)}
                    variant="contained"
                  >
                    Add Events
                  </Button>
                )}
              </Box>
              {!loading && (
                <Box style={{ marginBottom: 20 }}>
                  <EventsTable
                    open={open}
                    isTaskNew={isNew}
                    rows={evList}
                    employees={employees}
                    departments={departments}
                    customers={customers}
                    servicesproducts={servicesproducts}
                    isEditor={isEditor}
                    isRTL={isRTL}
                    words={words}
                    theme={theme}
                    taskId={row.id}
                    isNew={isNew}
                    employee={emplvalue}
                    department={departvalue}
                    customer={custvalue}
                    company={company}
                  ></EventsTable>
                  <Typography style={{ fontWeight: "bold", fontSize: 16 }}>
                    {words.total} : {moneyFormat(total)}
                  </Typography>
                </Box>
              )}
              {loading && <LoadingInline></LoadingInline>}
            </Box>
            {!isNew && (
              <Box
                m={1}
                display="flex"
                style={{ flex: 1, justifyContent: "flex-end" }}
              >
                <Button
                  size="medium"
                  color="primary"
                  variant="outlined"
                  onClick={() => setOpenInvoice(true)}
                >
                  {words.addInvoice}
                </Button>
              </Box>
            )}
          </Grid>
          <Grid item xs={2}></Grid>
        </Grid>
        <PopupTaskAppointment
          open={openEvent}
          onClose={() => setOpenEvent(false)}
          row={null}
          isNew={true}
          employees={employees}
          departments={departments}
          customers={customers}
          employee={emplvalue}
          department={departvalue}
          customer={custvalue}
          servicesproducts={servicesproducts}
          theme={theme}
          setEnd={setEnd}
          setEvList={setEvList}
        ></PopupTaskAppointment>
        <PopupTaskInvoice
          open={openInvoice}
          onClose={() => setOpenInvoice(false)}
          task={row}
          customers={customers}
          services={servicesproducts}
          employees={employees}
          departments={departments}
          company={company}
          theme={theme}
          items={itemsList}
        ></PopupTaskInvoice>
      </>
    </PopupLayout>
  );
};

export default PopupTask;
