import { gql } from "@apollo/client";

export default gql`
  query getEmployees($isRTL: Boolean, $resType: Int) {
    getEmployees(isRTL: $isRTL, resType: $resType) {
      ok
      error
      data {
        _id
        branch
        autoNo
        docNo
        name
        nameAr
        color
        resType

        phone
        email
        comPercent
        departmentId
        departmentName
        departmentNameAr
        departmentColor
        daysoff
        info
        userId

        createdAt
        updatedAt
      }
    }
  }
`;
