/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable @typescript-eslint/explicit-module-boundary-types */
import React, { useContext, useEffect, useState } from "react";
import Paper from "@material-ui/core/Paper";
import {
  EditingState,
  SortingState,
  IntegratedSorting,
  DataTypeProvider,
  SearchState,
  IntegratedFiltering,
} from "@devexpress/dx-react-grid";
import {
  Grid,
  TableHeaderRow,
  TableEditColumn,
  VirtualTable,
  Toolbar,
  SearchPanel,
} from "@devexpress/dx-react-grid-material-ui";
import { Command, Loading, PopupEditing } from "../../Shared";
import { getRowId } from "../../common";
import {
  createEvent,
  deleteEventById,
  getEvents,
  getLandingChartData,
  // getReminders,
  updateEvent,
} from "../../graphql";
import { useLazyQuery, useMutation } from "@apollo/client";
import {
  createdAtFormatter,
  currencyFormatter,
  doneFormatter,
  eventStatusFormatter,
  taskIdFormatter,
} from "../../Shared/colorFormat";
import PageLayout from "../main/PageLayout";
import { SearchTable } from "../../components";
import { getColumns } from "../../common/columns";
import PopupAppointment from "../../pubups/PopupAppointment";
import { EventsContext } from "../../contexts";
import DateNavigatorReports from "../../components/filters/DateNavigatorReports";
import { useMediaQuery } from "@material-ui/core";
import useTasks from "../../hooks/useTasks";
import getTasks from "../../graphql/query/getTasks";

export default function Appointments({
  isRTL,
  words,
  menuitem,
  isEditor,
  theme,
  employees,
  departments,
  company,
  servicesproducts,
}) {
  const col = getColumns({ isRTL, words });
  const isMobile = useMediaQuery("(max-width:600px)");

  const [columns] = useState([
    col.startDate,
    col.title,
    col.docNo,
    col.taskId,
    col.employee,
    col.department,
    col.customer,
    col.status,
    col.done,
    col.amount,
  ]);

  const [rows, setRows] = useState([]);
  const [loading, setLoading] = useState(false);
  const [start, setStart] = useState<any>(null);
  const [end, setEnd] = useState<any>(null);
  const [periodvalue, setPeriodvalue] = useState<any>(null);

  const { tasks } = useTasks();

  const {
    state: { currentDate, currentViewName, endDate, sort },
    dispatch,
  } = useContext(EventsContext);

  const currentViewNameChange = (e: any) => {
    dispatch({ type: "setCurrentViewName", payload: e.target.value });
  };
  const currentDateChange = (curDate: any) => {
    dispatch({ type: "setCurrentDate", payload: curDate });
  };
  const endDateChange = (curDate: any) => {
    dispatch({ type: "setEndDate", payload: curDate });
  };

  const [loadEvents, eventsData]: any = useLazyQuery(getEvents, {
    fetchPolicy: "cache-and-network",
  });

  const refresQuery = {
    refetchQueries: [
      {
        query: getEvents,
        variables: {
          start: start ? start.setHours(0, 0, 0, 0) : undefined,
          end: end ? end.setHours(23, 59, 59, 999) : undefined,
        },
      },
      {
        query: getLandingChartData,
      },
      {
        query: getTasks,
      },
      // {
      //   query: getReminders,
      // },
    ],
  };

  useEffect(() => {
    const variables = {
      start: start ? start.setHours(0, 0, 0, 0) : undefined,
      end: end ? end.setHours(23, 59, 59, 999) : undefined,
    };
    loadEvents({
      variables,
    });
  }, [start, end]);

  const [addEvent] = useMutation(createEvent, refresQuery);
  const [editEvent] = useMutation(updateEvent, refresQuery);
  const [removeEventById] = useMutation(deleteEventById, refresQuery);

  const commitChanges = async ({ deleted }) => {
    if (deleted) {
      const _id = deleted[0];
      removeEventById({ variables: { _id } });
    }
  };

  useEffect(() => {
    if (eventsData?.loading) {
      setLoading(true);
    }
    if (eventsData?.data?.getEvents?.data) {
      const { data } = eventsData.data.getEvents;
      setRows(data);
      setLoading(false);
    }
  }, [eventsData]);

  const refresh = () => {
    eventsData?.refetch();
  };

  const setSortDispatch = (value: any) => {
    dispatch({ type: "setSort", payload: value });
  };

  return (
    <PageLayout
      menuitem={menuitem}
      isRTL={isRTL}
      words={words}
      isEditor={isEditor}
      theme={theme}
      refresh={refresh}
      periodvalue={periodvalue}
      setPeriodvalue={setPeriodvalue}
    >
      <Paper>
        <DateNavigatorReports
          setStart={setStart}
          setEnd={setEnd}
          currentDate={currentDate}
          currentDateChange={currentDateChange}
          currentViewName={currentViewName}
          currentViewNameChange={currentViewNameChange}
          endDate={endDate}
          endDateChange={endDateChange}
          views={[1, 7, 30, 365, 1000]}
          isRTL={isRTL}
          words={words}
          theme={theme}
        ></DateNavigatorReports>
        <Grid rows={rows} columns={columns} getRowId={getRowId}>
          <SortingState
            defaultSorting={sort}
            onSortingChange={(srt: any) => setSortDispatch(srt)}
          />
          <EditingState onCommitChanges={commitChanges} />
          {!isMobile && <SearchState />}

          <IntegratedSorting />
          {!isMobile && <IntegratedFiltering />}

          <VirtualTable
            height={window.innerHeight - 133}
            messages={{
              noData: isRTL ? "  " : "no data",
            }}
            estimatedRowHeight={30}
          />
          <TableHeaderRow showSortingControls />

          <DataTypeProvider
            for={["startDate"]}
            formatterComponent={createdAtFormatter}
          ></DataTypeProvider>
          <DataTypeProvider
            for={["status"]}
            formatterComponent={eventStatusFormatter}
          ></DataTypeProvider>
          <DataTypeProvider
            for={["amount"]}
            formatterComponent={currencyFormatter}
          ></DataTypeProvider>
          <DataTypeProvider
            for={["done"]}
            formatterComponent={(props: any) =>
              doneFormatter({ ...props, editEvent })
            }
          ></DataTypeProvider>
          <DataTypeProvider
            for={["taskId"]}
            formatterComponent={(props: any) =>
              taskIdFormatter({ ...props, tasks })
            }
          ></DataTypeProvider>

          {isEditor && (
            <TableEditColumn
              showEditCommand
              showDeleteCommand
              showAddCommand
              commandComponent={Command}
            ></TableEditColumn>
          )}

          {!isMobile && <Toolbar />}
          {!isMobile && (
            <SearchPanel
              inputComponent={(props: any) => {
                return <SearchTable isRTL={isRTL} {...props}></SearchTable>;
              }}
            />
          )}
          <PopupEditing addAction={addEvent} editAction={editEvent}>
            <PopupAppointment
              employees={employees}
              departments={departments}
              company={company}
              servicesproducts={servicesproducts}
              theme={theme}
              tasks={tasks}
              isEditor={isEditor}
            ></PopupAppointment>
          </PopupEditing>
        </Grid>
        {loading && <Loading isRTL={isRTL} />}
      </Paper>
    </PageLayout>
  );
}
