/* eslint-disable array-callback-return */
/* eslint-disable @typescript-eslint/explicit-module-boundary-types */
import { Paper } from "@material-ui/core";
import React from "react";
import {
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ResponsiveContainer,
  AreaChart,
  Area,
} from "recharts";
import ChartHeader from "./ChartHeader";

export const EventsDaysChart = (props: any) => {
  const { data, pricolor, seccolor, dataKey, height, isRTL, prim } = props;
  const title = isRTL ? "  " : "Daily Appointments";
  return (
    <Paper style={{ height }}>
      <ChartHeader title={title} color={prim}></ChartHeader>
      <ResponsiveContainer width="100%" height={height - 30}>
        <AreaChart
          height={height}
          margin={{
            top: 20,
            right: 30,
            left: isRTL ? 40 : 10,
            bottom: 20,
          }}
          syncId="anyId"
          data={data}
        >
          <CartesianGrid strokeDasharray="3 3" vertical={false} />
          <XAxis dataKey="name" axisLine={false} tickLine={false} />
          <YAxis
            axisLine={false}
            tickLine={false}
            mirror={isRTL ? true : false}
          />
          <Tooltip
            formatter={(value: any, name: string) => [
              value,
              name === "count" ? (isRTL ? "" : "All") : name,
            ]}
          />
          <Legend
            formatter={(value) => {
              return value === "count"
                ? isRTL
                  ? " "
                  : "All Appointments"
                : isRTL
                ? " "
                : "Completed";
            }}
          />

          <Area
            type="monotone"
            dataKey={dataKey}
            fill={seccolor}
            stroke={seccolor}
          />
          <Area
            type="monotone"
            dataKey={isRTL ? "" : "Completed"}
            stroke={pricolor}
            fill={pricolor}
          />
        </AreaChart>
      </ResponsiveContainer>
    </Paper>
  );
};
export default EventsDaysChart;
