import { gql } from "@apollo/client";

export default gql`
  query getDepartments($isRTL: Boolean) {
    getDepartments(isRTL: $isRTL) {
      ok
      error
      data {
        _id
        branch
        autoNo
        docNo
        name
        nameAr
        color
        desc
        userId

        createdAt
        updatedAt
      }
    }
  }
`;
