/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable @typescript-eslint/explicit-module-boundary-types */
import React, { useContext, useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import { invoiceClasses } from "../themes";
import { useCustomers, useLastNos } from "../hooks";
import { dublicateAlert, errorAlert, messageAlert } from "../Shared";
import { GContextTypes } from "../types";
import { GlobalContext } from "../contexts";

import { Box, TextField, Typography } from "@material-ui/core";
import ServiceItemForm from "../Shared/ServiceItemForm";
import ItemsTable from "../Shared/ItemsTable";
import { PriceTotal } from "../Shared/TotalPrice";
import { operationTypes } from "../constants";
import { useMutation } from "@apollo/client";
import {
  createInvoice,
  getInvoices,
  getLandingChartData,
  getLastNos,
} from "../graphql";
import { accountCode } from "../constants/kaid";
import { print } from "../common";
import PaymentSelect from "../pages/options/PaymentSelect";
import { moneyFormat } from "../Shared/colorFormat";
import PopupLayout from "../pages/main/PopupLayout";
import { Grid } from "@material-ui/core";
import AutoFieldLocal from "../components/fields/AutoFieldLocal";
import { CalenderLocal } from "../components";
import { getAppStartEndPeriod } from "../common/time";
import PopupCustomer from "./PopupCustomer";

export const indexTheList = (list: any) => {
  return list.map((item: any, index: any) => {
    return {
      ...item,
      index,
    };
  });
};

const PopupAppointInvoice = ({
  open,
  onClose,
  appoint,
  services,
  editEvent,
  employees,
  departments,
  company,
  isNew = true,
  theme,
  items,
}: any) => {
  const classes = invoiceClasses();
  const [alrt, setAlrt] = useState({ show: false, msg: "", type: undefined });
  const [selectedDate, setSelectedDate] = React.useState(new Date());
  const [invNo, setInvNo] = useState<any>("");

  const [itemsList, setItemsList] = useState<any>([]);
  const [accounts, setAccounts] = useState<any>([]);
  const [ptype, setPtype] = useState<any>("cash");

  const [discount, setDiscount] = useState(0);
  const [totals, setTotals] = useState<any>({});

  const [custvalue, setCustvalue] = useState<any>(null);

  const [openCust, setOpenCust] = useState(false);
  const [newtext, setNewtext] = useState("");

  const [isCash, setIsCash] = useState(false);
  const { customers, addCustomer, editCustomer } = useCustomers();

  const { handleSubmit, reset } = useForm({});
  const {
    translate: { words, isRTL },
    store: { user },
  }: GContextTypes = useContext(GlobalContext);

  const refresQuery = {
    refetchQueries: [
      {
        query: getInvoices,
        variables: {
          start: new Date().setHours(0, 0, 0, 0),
          end: new Date().setHours(23, 59, 59, 999),
        },
      },
      {
        query: getLastNos,
      },
      {
        query: getLandingChartData,
      },
    ],
  };

  const [addInvoice] = useMutation(createInvoice, refresQuery);

  const { lastNos, freshlastNos } = useLastNos();

  const openCustomer = () => {
    setOpenCust(true);
  };
  const onCloseCustomer = () => {
    setOpenCust(false);
    setNewtext("");
  };
  const onNewFieldChange = (nextValue: any) => {
    setCustvalue(nextValue);
  };

  const resetAllForms = () => {
    reset();
    setItemsList([]);
    setDiscount(0);
    setTotals({});
    setCustvalue(null);
    setInvNo("");
    setAccounts([]);
    setPtype("");
    setIsCash(false);
    setSelectedDate(new Date());
  };

  const addItemToList = (item: any) => {
    const newArray = [...itemsList, { ...item, userId: user._id }];
    const listwithindex = indexTheList(newArray);
    setItemsList(listwithindex);
  };
  const editItemInList = (item: any) => {
    const newArray = itemsList.map((it: any) => {
      if (it._id === item._id) {
        return item;
      } else {
        return it;
      }
    });
    const listwithindex = indexTheList(newArray);
    setItemsList(listwithindex);
  };

  const removeItemFromList = (index: any) => {
    const newList = [...itemsList];
    newList.splice(index, 1);
    const listwithindex = indexTheList(newList);
    setItemsList(listwithindex);
  };

  const handleDateChange = (date: any) => {
    setSelectedDate(date);
  };

  useEffect(() => {
    if (isNew && lastNos) {
      setInvNo(lastNos?.salesInvoice ? Number(lastNos?.salesInvoice) + 1 : 1);
    }
  }, [open]);

  useEffect(() => {
    getOverallTotal();
  }, [itemsList, discount, ptype, isCash]);

  useEffect(() => {
    if (appoint && appoint.startDate) {
      const _id = appoint.customerId;
      const cust = customers.filter((it: any) => it._id === _id)[0];
      setCustvalue(cust);
    }
    if (items) {
      setItemsList(items);
    }
  }, [appoint, services, customers, items]);

  const getOverallTotal = () => {
    const totalsin = itemsList.map((litem: any) => litem.itemtotal);
    const sum = totalsin.reduce((psum: any, a: any) => psum + a, 0);
    const costtotals = itemsList.map((litem: any) => litem.itemtotalcost);
    const costsum = costtotals.reduce((psum: any, a: any) => psum + a, 0);
    const amount = sum - discount;
    const profit = sum - discount - costsum;
    const tots = {
      itemsSum: amount,
      itemsCostSum: costsum,
      costAmount: costsum,
      total: sum,
      amount,
      profit,
    };
    setTotals(tots);
    const accs = [
      {
        debitAcc: accountCode.accounts_receivable,
        creditAcc: accountCode.sales_income,
        amount: sum,
        type: operationTypes.salesInvoice,
      },
      {
        debitAcc: accountCode.sales_income,
        creditAcc: accountCode.accounts_receivable,
        amount: discount,
        type: operationTypes.customerDiscount,
      },
      {
        debitAcc:
          ptype === "cash" ? accountCode.cash_on_hand : accountCode.card,
        creditAcc: accountCode.accounts_receivable,
        amount: isCash ? sum - discount : 0,
        type: operationTypes.customerReceipt,
      },
    ];
    setAccounts(accs);
  };

  const onSubmit = async () => {
    const { startPeriod, endPeriod } = getAppStartEndPeriod();
    if (selectedDate < startPeriod || selectedDate > endPeriod) {
      await messageAlert(
        setAlrt,
        isRTL ? "  " : "Date should be change"
      );
      return;
    }
    if (discount < 0) {
      await messageAlert(
        setAlrt,
        isRTL ? "     " : "Discount can't be minus"
      );
      return;
    }
    if (discount > totals?.total) {
      await messageAlert(
        setAlrt,
        isRTL
          ? "        "
          : "Discount can't be biger than Total"
      );
      return;
    }
    if (!custvalue) {
      await messageAlert(
        setAlrt,
        isRTL ? "   " : "Please add Customer"
      );
      return;
    }
    if (isNew && Number(invNo) <= Number(lastNos.salesInvoice)) {
      await messageAlert(
        setAlrt,
        isRTL
          ? `Invoice no must be more than ${lastNos.salesInvoice}`
          : `       ${lastNos.salesInvoice}`
      );
      return;
    }
    if (!itemsList || itemsList.length === 0) {
      await messageAlert(
        setAlrt,
        isRTL
          ? `       ${lastNos.salesInvoice}`
          : `Invoice no must be more than ${lastNos.salesInvoice}`
      );
      return;
    }
    const { amount, costAmount, profit, total } = totals;

    const variables: any = {
      docNo: invNo ? invNo.toString() : undefined,
      time: selectedDate,
      customer: {
        customerId: custvalue._id,
        customerName: custvalue.name,
        customerNameAr: custvalue.nameAr,
        customerPhone: custvalue.phone,
      },
      items: JSON.stringify(itemsList),
      costAmount,
      total,
      discount,
      amount,
      profit,
      isPaid: isCash,
      isCash,
      amountPaid: isCash ? amount : 0,
      accounts,
      paymentType: ptype,
      userId: user._id,
      eventId: appoint.id,
      eventNo: appoint.docNo,
    };

    apply(addInvoice, variables);
  };

  const apply = async (mutate: any, variables: any) => {
    try {
      await mutate({ variables });
      // handlePrint();
      // await successAlert(setAlrt);
      await editEvent({ variables: { id: appoint.id, status: 10 } });
      freshlastNos();
      onCloseForm();
    } catch (error) {
      onError(error);
      console.log(error);
    }
  };

  const onError = async (error: any) => {
    if (error.message.includes("duplicate")) {
      await dublicateAlert(setAlrt, isRTL);
    } else {
      await errorAlert(setAlrt, isRTL);
      reset();
      console.log(error);
    }
  };

  const handlePrint = () => {
    const time = new Date(selectedDate);
    // const { name, nameAr, phone } = custvalue;
    let employee = "";
    const items = itemsList.map((it: any) => {
      if (!employee && it.employeeName) {
        employee = `${it.employeeName} / ${it.employeeNameAr} `;
      }
      return {
        name: it.name,
        nameAr: it.nameAr,
        price: it.itemprice,
        qty: it.itemqty,
        total: it.itemtotal,
      };
    });
    const payment = ptype === "cash" ? "Cash Payment" : "Card Payment";
    const paymentAr = ptype === "cash" ? " " : " ";
    const { total } = totals;
    const amount = total - discount;
    const data = {
      time: time.toLocaleTimeString(navigator.language, {
        hour: "2-digit",
        minute: "2-digit",
      }),
      date: time.toLocaleDateString("en-GB"),
      invNo,
      items,
      discount: moneyFormat(discount),
      amount: moneyFormat(amount),
      logo: company?.logo ? company?.logo : "logo.jpg",
      phone: company.tel1,
      mobile: company.mob,
      address: company.address,
      company: isRTL ? company.nameAr : company.name,
      employee,
      user: user.name ? user.name : user.username,
      payment,
      paymentAr,
    };
    print(data);
  };
  const onCloseForm = () => {
    resetAllForms();
    onClose();
  };

  const onHandleSubmit = () => {
    handleSubmit(onSubmit)();
  };

  const title = isRTL
    ? isNew
      ? " "
      : " "
    : isNew
    ? "New Invoice"
    : "Edit Invoice";

  return (
    <PopupLayout
      isRTL={isRTL}
      open={open}
      onClose={onCloseForm}
      title={title}
      onSubmit={onHandleSubmit}
      theme={theme}
      alrt={alrt}
      print={handlePrint}
      maxWidth="md"
      mt={0}
      mb={50}
    >
      <Grid container spacing={0}>
        <Grid item xs={4}>
          <CalenderLocal
            isRTL={isRTL}
            label={words.time}
            value={selectedDate}
            onChange={handleDateChange}
          ></CalenderLocal>
        </Grid>
        <Grid item xs={8}>
          <PaymentSelect
            words={words}
            ptype={ptype}
            isCash={isCash}
            setIsCash={setIsCash}
            setPtype={setPtype}
          ></PaymentSelect>
        </Grid>
        <Grid item xs={7}>
          <AutoFieldLocal
            name="customer"
            title={words.customer}
            words={words}
            options={customers}
            value={custvalue}
            setSelectValue={setCustvalue}
            isRTL={isRTL}
            fullWidth
            openAdd={openCustomer}
            showphone
          ></AutoFieldLocal>
        </Grid>
        <Grid item xs={3}></Grid>
        <Grid item xs={2}>
          <Box
            display="flex"
            style={{
              flex: 1,
              flexDirection: isRTL ? "row-reverse" : "row",
              alignItems: "center",
              justifyContent: "flex-end",
              marginLeft: isRTL ? undefined : 20,
              marginRight: isRTL ? 20 : undefined,
            }}
          >
            {isNew && (
              <Typography style={{ color: "#777" }}>{words.no}</Typography>
            )}
            <TextField
              name="invNo"
              disabled={!isNew}
              value={invNo}
              onChange={(e: any) => setInvNo(Number(e.target.value))}
              variant="outlined"
              style={{ width: isNew ? 70 : 100, marginLeft: 20 }}
              margin="dense"
              // type="number"
              inputProps={{
                style: {
                  textAlign: "center",
                  fontSize: 14,
                  height: 13,
                },
              }}
            />
          </Box>
        </Grid>
        <Grid item xs={12}>
          <Box
            style={{
              backgroundColor: "#f4f4f4",
              padding: 10,
              marginTop: 15,
              marginBottom: 15,
              borderRadius: 10,
            }}
          >
            <Box display="flex">
              <ServiceItemForm
                options={services}
                addItem={addItemToList}
                words={words}
                employees={employees}
                departments={departments}
                classes={classes}
                user={user}
                isRTL={isRTL}
              ></ServiceItemForm>
            </Box>

            <Box style={{ marginBottom: 20 }}>
              <ItemsTable
                rows={itemsList}
                editItem={editItemInList}
                removeItem={removeItemFromList}
                isRTL={isRTL}
                words={words}
                user={user}
              ></ItemsTable>
            </Box>
          </Box>
          <Box
            display="flex"
            style={{
              alignItems: "center",
              justifyContent: "space-between",
            }}
          >
            <TextField
              name="discount"
              label={words.discount}
              value={discount.toString()}
              onChange={(e: any) => setDiscount(Number(e.target.value))}
              variant="outlined"
              style={{ width: 200 }}
              margin="dense"
              type="number"
              onFocus={(e) => e.target.select()}
            />

            <PriceTotal
              amount={totals?.amount}
              total={totals?.total}
              discount={discount}
              words={words}
            ></PriceTotal>
          </Box>
          <PopupCustomer
            newtext={newtext}
            open={openCust}
            onClose={onCloseCustomer}
            isNew={true}
            setNewValue={onNewFieldChange}
            row={null}
            addAction={addCustomer}
            editAction={editCustomer}
          ></PopupCustomer>
        </Grid>
      </Grid>
    </PopupLayout>
  );
};

export default PopupAppointInvoice;
